import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { useFormik } from "formik";
import * as Yup from "yup";
import { addcontactApi, infoApi, platformApi } from "../../apis/services";
import { FaPinterestP, FaXTwitter, FaFacebookF, FaInstagram } from "react-icons/fa6";
import { successNotification } from "../Toast";
import { errorResponseHandler } from "../../apis/http";
import { FaLinkedinIn, FaTiktok } from "react-icons/fa";


const validationSchema = Yup.object({
  fname: Yup.string().required("First name is required"),
  lname: Yup.string().required("Last name is required"),
  email: Yup.string()
    .email("Invalid email address")
    .required("Email is required"),
  phone: Yup.string()
    .matches(/^[0-9]{10}$/, "Phone number must be 10 digits")
    .required("Phone is required"),
  subject: Yup.string().required("Please select a subject"),
  message: Yup.string().required("Message is required"),
  agree: Yup.boolean().oneOf([true], "You must accept the Privacy Policy"),
});

const ContactUs = () => {
  const [data, setData] = useState();
  const [infoData, setInFoData] = useState([])

  const formik = useFormik({
    initialValues: {
      fname: "",
      lname: "",
      email: "",
      phone: "",
      subject: "",
      message: "",
      agree: false,
    },
    validationSchema,
    validateOnChange: false,
    validateOnBlur: true, 
    onSubmit: async (values, { resetForm }) => {
      try {
        const payload = {
          first_name: values.fname,
          last_name: values.lname,
          phone_number: values.phone,
          enquiry_type: values.subject,
          email: values.email,
          message: values.message,
          agreed_to_terms: values.agree,
        };
        await addcontactApi(payload);

        successNotification("Message sent successfully!");
        resetForm();
      } catch (error) {
        errorResponseHandler(error);
      }
    },
  });

  const handleFormSubmit = async (e) => {
    e.preventDefault();

    try {
      const errors = await formik.validateForm(); 
      const errorKeys = Object.keys(errors);

      if (errorKeys.length > 0) {
        const firstKey = errorKeys[0];
        const touchedObj = {};
        Object.keys(formik.initialValues).forEach((k) => {
          touchedObj[k] = false;
        });
        touchedObj[firstKey] = true;
        formik.setTouched(touchedObj);

        const el = document.getElementsByName(firstKey)[0];
        if (el && typeof el.focus === "function") {
          el.focus();
        }
        return;
      }

      await formik.submitForm();
    } catch (err) {
      errorResponseHandler(err)
    }
  };

  const fetchData = async () => {
    try {
      const response = await platformApi();
      setData(response?.platforms?.[0]);
    } catch (error) {
      errorResponseHandler(error);
    }
  };


  const fetchInfoata = async () => {
    try {
      const response = await infoApi();
      setInFoData(response?.info?.[0]);
    } catch (error) {
      errorResponseHandler(error);
    }
  };

  useEffect(() => {
    fetchData();
    fetchInfoata();
  }, []);

  return (
    <>
      <input type="checkbox" id="mpclose" />
      <div className="page-header bg-section dark-section">
        <div className="container">
          <div className="row">
            <div className="col-lg-12">
              <div className="page-header-box">
                <h1 className="text-anime-style-3" data-cursor="-opaque">
                  Contact Us
                </h1>
                <nav className="wow fadeInUp">
                  <ol className="breadcrumb">
                    <li className="breadcrumb-item">
                      <Link to="/">home </Link>{" "}
                    </li>
                    <li className="breadcrumb-item">
                      <i className="fa fa-chevron-right" aria-hidden="true"></i>
                    </li>
                    <li className="breadcrumb-item active" aria-current="page">
                      Contact Us
                    </li>
                  </ol>
                </nav>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="page-contact-us">
        <div className="container">
          <div className="row align-items-center">
            <div className="col-lg-6 col-md-8">
              <div className="section-title">
                <h3 className="wow fadeInUp">contact us</h3>
                <h2 className="text-anime-style-3" data-cursor="-opaque">
                  Joining Us in Creating Something Great
                </h2>
              </div>
            </div>

            <div className="col-lg-6 col-md-4">
              <div className="contact-now-circle">
                <img src="images/flask-1.png" alt="" />
              </div>
            </div>
          </div>

          <div className="row align-items-center">
            <div className="col-lg-6">
              <div className="contact-form wow fadeInUp" data-wow-delay="0.2s">
                <small className="form-text text-muted mb-4 d-block">
                  Note: <Link to="/privacy-policy">Do not include medical information </Link>
                </small>

                <form onSubmit={handleFormSubmit}>
                  <div className="row">
                    <div className="form-group col-md-6 mb-4">
                      <input
                        type="text"
                        name="fname"
                        className="form-control"
                        placeholder="First Name"
                        {...formik.getFieldProps("fname")}
                      />
                      {formik.touched.fname && formik.errors.fname && (
                        <div className="text-danger">{formik.errors.fname}</div>
                      )}
                    </div>

                    <div className="form-group col-md-6 mb-4">
                      <input
                        type="text"
                        name="lname"
                        className="form-control"
                        placeholder="Last Name"
                        {...formik.getFieldProps("lname")}
                      />
                      {formik.touched.lname && formik.errors.lname && (
                        <div className="text-danger">{formik.errors.lname}</div>
                      )}
                    </div>

                    <div className="form-group col-md-6 mb-4">
                      <input
                        type="email"
                        name="email"
                        className="form-control"
                        placeholder="Email Address"
                        {...formik.getFieldProps("email")}
                      />
                      {formik.touched.email && formik.errors.email && (
                        <div className="text-danger">{formik.errors.email}</div>
                      )}
                    </div>

                    <div className="form-group col-md-6 mb-4">
                      <input
                        type="text"
                        name="phone"
                        className="form-control"
                        placeholder="Phone Number"
                        {...formik.getFieldProps("phone")}
                      />
                      {formik.touched.phone && formik.errors.phone && (
                        <div className="text-danger">{formik.errors.phone}</div>
                      )}
                    </div>

                    <div className="form-group col-md-12 mb-4">
                      <select
                        name="subject"
                        className="form-control"
                        {...formik.getFieldProps("subject")}
                      >
                        <option value="" disabled>
                          Select inquiry type
                        </option>
                        <option value="General Inquiry">Support</option>
                        <option value="Partnerships">Feedback</option>
                        <option value="Press & Media">Partnership</option>
                        <option value="Support">Press</option>
                        <option value="Security">Security</option>
                      </select>
                      {formik.touched.subject && formik.errors.subject && (
                        <div className="text-danger">{formik.errors.subject}</div>
                      )}
                    </div>

                    <div className="form-group col-md-12 mb-4">
                      <textarea
                        name="message"
                        className="form-control"
                        rows="4"
                        placeholder="Message"
                        {...formik.getFieldProps("message")}
                      />
                      {formik.touched.message && formik.errors.message && (
                        <div className="text-danger">{formik.errors.message}</div>
                      )}
                    </div>

                    <div className="form-group col-md-12 mb-4">
                      <div>
                        <input
                          className="form-check-input"
                          type="checkbox"
                          id="flexCheckDefault"
                          name="agree"
                          checked={formik.values.agree}
                          onChange={formik.handleChange}
                          onBlur={formik.handleBlur}
                        />
                        <label className="form-check-label" htmlFor="flexCheckDefault">
                          I agree to the <Link to="/privacy-policy">Privacy Policy</Link>
                        </label>
                      </div>
                      {formik.touched.agree && formik.errors.agree && (
                        <div className="text-danger">{formik.errors.agree}</div>
                      )}
                    </div>

                    <div className="col-lg-12">
                      <div className="contact-form-btn">
                        <button type="submit" className="btn-default">
                          Send Message
                        </button>
                      </div>
                    </div>
                  </div>
                </form>

              </div>
            </div>

            <div className="col-lg-6">
              <div className="contact-us-content wow fadeInUp" data-wow-delay="0.4s">
                <div className="contact-info-list">
                  <div className="contact-info-item">
                    <div className="icon-box">
                      <img src="images/icon-location.svg" alt="" />
                    </div>
                    <div className="contact-info-content">
                      <h3>office address</h3>
                      <p>{infoData?.office_address}</p>
                    </div>
                  </div>

                  <div className="contact-info-item">
                    <div className="icon-box">
                      <img src="images/icon-mail.svg" alt="" />
                    </div>
                    <div className="contact-info-content">
                      <h3>Partnerships and Support </h3>
                      <p><a href={infoData?.partnership_and_support}>{infoData?.partnership_and_support}</a></p>
                      <h3>Press and Security</h3>
                      <p><a href={infoData?.partnership_and_support}>{infoData?.partnership_and_support} </a></p>
                    </div>
                  </div>

                  <div className="contact-info-item">
                    <div className="icon-box">
                      <img src="images/icon-phone.svg" alt="" />
                    </div>
                    <div className="contact-info-content">
                      <h3>call us</h3>
                      <p><a href={infoData?.call_us}>{infoData?.call_us}</a></p>
                     
                    </div>
                  </div>

                  <div className="contact-info-item">
                    <div className="icon-box">
                      <img src="images/icon-clock.svg" alt="" />
                    </div>
                    <div className="contact-info-content">
                      <h3>Working Hours</h3>
                      <p>{infoData?.working_hours}</p>
                    </div>
                  </div>
                </div>

                <div className="contact-social-list">
                  <h3>stay connected:</h3>
                  <ul>
                    <li>
                      <a href={data?.pinterest} target="_blank" rel="noreferrer"><FaLinkedinIn /></a>
                    </li>
                    <li>
                      <a href={data?.twitter} target="_blank" rel="noreferrer"><FaTiktok /></a>
                    </li>
                    <li>
                      <a href={data?.facebook} target="_blank" rel="noreferrer"><FaFacebookF /></a>
                    </li>
                    <li>
                      <a href={data?.instagram} target="_blank" rel="noreferrer"><FaInstagram /></a>
                    </li>
                  </ul>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="google-map">
        <div className="container">
          <div className="row">
            <div className="col-lg-12">
              <div className="google-map-iframe">
                <iframe
                  src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d96737.10562045308!2d-74.08535042841811!3d40.739265258395164!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89c24fa5d33f083b%3A0xc80b8f06e177fe62!2sNew%20York%2C%20NY%2C%20USA!5e0!3m2!1sen!2sin!4v1703158537552!5m2!1sen!2sin"
                  allowFullScreen=""
                  loading="lazy"
                  referrerPolicy="no-referrer-when-downgrade"
                  title="map"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ContactUs;
